#weather-widget {
    display: flex;
    flex-direction: row; 
    align-items: flex-start;
    justify-content: space-between;
    background-color: #b2bdc5; 
    border-radius: 5px; 
    padding: 1px; 
    width: 375px; 
    box-shadow: 0px 0px 7px 0px rgba(0, 0, 0, 0.7);
}

#today-weather {
    /* flex: 1; */
    text-align: center;
    padding-right: 1px; 
    border-radius: 1px;
    padding: 1px; 
    margin-left: 35px;
    margin-right: flex;
}

#today-weather h2 {
    font-size: 1em; 
    font-weight: 600; 
    color: #000000; 
    margin-bottom: 1px; 
    margin-top: 8px; 
}

#today-weather img {
    width: 50px; 
    height: 50px; 
    margin-bottom: -15px;
    margin-top: -10px;
}

#weather-temp-description {
    display: flex; 
    align-items: center;
    justify-content: center; 
    margin-top: 5px; 
}

#weather-temp {
    font-size: 1em; 
    font-weight: 600;
    color: #000000; 
    margin-right: 5px; 
}

#weather-description {
    font-size: 1em;
    font-weight: 600; 
    color: #000000; 
}


/* Forecast css */
.forecast-day {
    display: flex;
    flex-direction: column; 
    align-items: center; 
    width: auto;
    margin-bottom: 1px;
    padding: 8px;
    transition: background-color 0.3s; 
    padding-right: 1px; 
    margin-right: 35px;
    margin-left: flex;
}

.forecast-day img {
    width: 50px; 
    height: 50px;
    margin-top: -10px;
    margin-bottom: -5px;
}

.forecast-day-info {
    display: flex;
    flex-direction: row; 
    align-items: center; 
}

.forecast-temp {
    font-size: 1.2em; 
    font-weight: 600; 
    color: #000000;
}

.forecast-day span {
    font-size: 1em; 
    font-weight: 600; 
    color: #010000; 
    text-transform: capitalize; 
    margin-bottom: 1px; 
}
  
  /* Default night background */
  #weather-widget.night {
    background: #536c84; /* Dark blue for night */
  }

.loading {
    font-size: 1.5rem;
    color: #ffffff;
    text-align: center;
    padding: 20px;
  }

  #weather-widget {
    transition: background 0.5s ease-in-out;
  }