.header-wrapper {
    display: flex; /* Use flexbox for horizontal alignment */
    align-items: center; /* Vertically center the items */
    gap: 5px; /* Optional: Adds space between the h1 and the icon */
  }


  h1 {
    font-size: 20px;
    margin-bottom: 0.5px;
    margin-top: 1px;
    color: #ffffff; /* Light blue color for the title */
  }
  

  #container div {
    margin: 2px 0;
    font-size: 14px;
    /* font-weight: bold; */
    font-family: Verdana, Geneva, Tahoma, sans-serif;
  }
  
  #cycle-code {
    color: #00bfff; /* Light blue for cycle code */
  }
  
  #effective-date {
    color: #70f570; /* Light green for effective date */
  }
  
  #expiration-date {
    color: #ef5c42; /* Light coral for expiration date */
  }
  
  